import React from 'react'
import {Grid, Cell } from 'react-mdl';
import krisImage from './assets/kristopher.jpg';
import { Spring } from 'react-spring/renderprops';

export const  About = () => (
<div>  

<Spring
from={{ opacity: 0, marginLeft: -500 }}
to={{ opacity: 1, marginLeft:0 }}

>
{ props => (
    <div style={props}> 


<div className="about-body">
    <Grid className="about-grid">
        <Cell col={3} style={{color:'white'}} > 
        <img className="roundedImage" src={krisImage} alt="Kristopher" />

        </Cell>

{/*  Skills to experience  */}

        <Cell col={8} className="about-grid-boxes"> 
        <h3> <strong > Languages </strong> </h3>
    <p className="paragraphText"> JavaScript </p>
    <p className="paragraphText"> Java </p>
    <p className="paragraphText"> C/C++ </p>
    <p className="paragraphText"> C# </p>


        </Cell>


{/*  Skills to experience  */}

        <Cell col={3} > 
        
        </Cell>

        <Cell col={8} className="about-grid-boxes"> 
        <h3> <strong> Experience </strong> </h3>

        
        <h4> <strong> Leonardo  Corp – Junior Systems Admin (Co-Op) </strong>	<br />	  </h4>   
        <p> 
   September 2019 - May 2020 (Extended 4 Months)<br />
        </p>
        
       
       <h4> <strong> Seneca College - ITS Desk - Team Leader </strong>  <br /> </h4>
        <p>   
     April 2018 - August 2018<br />

        </p>

        </Cell>



    </Grid>
    
    </div>
   


    </div>
)}
</Spring>


    </div>
)