 import React from 'react'
 import { About } from './About';
import {Grid, Cell } from 'react-mdl';
import krisImage from './assets/kristopher.jpg';
import { Spring } from 'react-spring/renderprops';
import linkedinImage from './assets/linkedin.png';
import githubimage from './assets/github.png';


 export const  Home = () => (
     
<Spring
from={{ opacity: -7, marginTop: -500 }}
to={{ opacity: 1, marginTop:0 }}
>
{ props => (

    <div style={props}>    
    <div  style={{textAlign:'center',margin:'20px'}}>  



<img className="roundedImage" src={krisImage} alt="Kristopher" /> <br/><br/>
<div className="homeMain" >


<h1> <strong> Kris Vala </strong> </h1>
<p className="paragraphText"> Hi and welcome to my personal website. I'm a recent software development graduate with a passion for creating and building new and exciting applications. 
     </p>
     <br/>
     <p className="paragraphText">
I'm currently based in Toronto and am very excited about the future.<br/>
 I take every day as a learning experience and am always eager to learn new technologies and further develop my skills.
     </p>
<br/>
</div>

<br></br>
<div className="homeMain" >

<h1> <strong> CONNECT </strong> </h1>
<p className="paragraphText"> Please explore my website and take a look at the links below. Feel free to shoot me a message on LinkedIn to get in contact with me.
     </p>

<a href ="https://github.com/KristopherVala"><img className="thumbnail" src={githubimage} alt="Github"/></a>
<a href ="https://www.linkedin.com/in/kristophervala"><img className="thumbnail" src={linkedinImage} alt="Linkedin"/> </a>

</div>

</div>
</div>

    )}

</Spring>





 )